html {
  color-scheme: light dark;
  font-family: system-ui;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
}

body {
  background: url('./assets/bg-image3.jpg') no-repeat center center fixed;
  background-size: 100% 100%;
  display: flex;
  flex-direction: column;
  width: 100%;
  min-height: 100vh;
  overflow: hidden;
  padding-top: 200;
  padding-bottom: 50;
}

.main {
  flex: 1;
  width: 100%;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.title {
  font-size: 28;
  font-weight: 800;
  margin-bottom: 28;
  color: white;
  letter-spacing: 1;
  padding: 10px 26px;
  border-radius: 18;
  display: inline-block;
  backdrop-filter: blur(10px);
  background: linear-gradient(145deg, rgba(255, 255, 255, 0.22), rgba(255, 255, 255, 0.08));
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.25), inset 0 0 10px rgba(255, 215, 0, 0.25);
  border: 1.5px solid rgba(255, 255, 255, 0.25);
  text-shadow: 0 0 6px rgba(255, 215, 0, 0.6);
}

.btn-game {
  font-family: 'Press Start 2P', monospace;
  font-size: 16px;
  letter-spacing: 1.5px;
  padding: 18px 34px;
  border-radius: 14px;
  text-transform: uppercase;
  cursor: pointer;
  transition: all 0.2s ease;
  border: none;
  outline: none;
  position: relative;
  color: #fff;
  text-shadow: 0 0 6px rgba(255, 255, 255, 0.7);
  box-shadow: 0 0 12px rgba(255, 255, 255, 0.3);
}

.small {
  font-size: 12px;
  letter-spacing: 1.5px;
  padding: 14px 30px;
}

.btn-game::before {
  content: '';
  position: absolute;
  inset: 0;
  border-radius: 14px;
  padding: 2px;
  background: linear-gradient(135deg, #8f00ff, #00ffff, #ffffff);
  -webkit-mask:
    linear-gradient(#fff 0 0) content-box,
    linear-gradient(#fff 0 0);
  -webkit-mask-composite: xor;
  mask-composite: exclude;
  animation: glowBorder 2s linear infinite;
}

@keyframes glowBorder {
  0% {
    filter: hue-rotate(0deg);
  }

  100% {
    filter: hue-rotate(360deg);
  }
}

/* === START BUTTON === */
.btn-game.start {
  background: radial-gradient(circle at top left, #00bfff, #7b68ee);
  box-shadow: 0 0 25px #00ffff, 0 0 50px rgba(123, 104, 238, 0.7);
}

.btn-game.start.small {
  background: radial-gradient(circle at top left, #00bfff, #7b68ee);
  box-shadow: 0 0 5px #00ffff, 0 0 1px rgba(123, 104, 238, 0.7);
}

.btn-game.start:hover {
  transform: scale(1.05);
  box-shadow: 0 0 35px #00ffff, 0 0 90px rgba(123, 104, 238, 1);
}

.btn-game.start.small:hover {
  background: radial-gradient(circle at top left, #00bfff, #7b68ee);
  box-shadow: 0 0 5px #00ffff, 0 0 1px rgba(123, 104, 238, 0.7);
}

/* === STOP BUTTON === */
.btn-game.stop {
  background: radial-gradient(circle at bottom right, #fff700, #ffcc00);
  color: #222;
  text-shadow: 0 0 4px rgba(255, 255, 255, 0.6);
  box-shadow: 0 0 25px #fff200, 0 0 50px rgba(255, 255, 0, 0.6);
}

.btn-game.stop:hover {
  transform: scale(1.05);
  box-shadow: 0 0 40px #fff700, 0 0 90px rgba(255, 255, 0, 0.9);
}

.btn-edit {
  position: absolute;
  top: 16px;
  right: 16px;
  width: 38px;
  height: 38px;
  border: none;
  border-radius: 12px;
  backdrop-filter: blur(12px);
  background: rgba(255, 255, 255, 0.15);
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all 0.25s ease;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.25);
}

.btn-edit:hover {
  background: rgba(255, 255, 255, 0.3);
  transform: scale(1.1);
  box-shadow: 0 6px 25px rgba(0, 0, 0, 0.35);
}

.btn-edit i {
  font-size: 16px;
  color: #fff;
}

/* efek tekan */
.btn-game:active {
  transform: scale(0.93);
  box-shadow: 0 0 10px rgba(255, 255, 255, 0.4);
}


.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 99;
}

.modal-box {
  background: white;
  padding: 20px;
  width: 50vh;
  max-height: 80vh;
  overflow-y: auto;
  border-radius: 8px;
  color: #616161;
}

.nomor-list {
  margin: 15px 0;
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
}

.nomor-item {
  border: 1px solid #ddd;
  padding: 6px 10px;
  border-radius: 4px;
  display: flex;
  align-items: center;
  gap: 6px;
}

.modal-actions {
  margin-top: 15px;
  display: flex;
  justify-content: flex-end;
  gap: 10px;
}

button {
  padding: 6px 12px;
  cursor: pointer;
}

button.actions {
  padding: 6px 12px;
  border-radius: 6;
  cursor: pointer;
  background: #333;
  color: white;
  border: 1px solid #666;
}