html {
  color-scheme: light dark;
  justify-content: center;
  align-items: center;
  height: 100%;
  font-family: system-ui;
  display: flex;
}

body {
  background: url("bg-image3.43c360f5.jpg") center / 100% 100% no-repeat fixed;
  flex-direction: column;
  width: 100%;
  min-height: 100vh;
  padding-top: 200px;
  padding-bottom: 50px;
  display: flex;
  overflow: hidden;
}

.main {
  flex: 1;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100vh;
  display: flex;
}

.title {
  color: #fff;
  letter-spacing: 1px;
  backdrop-filter: blur(10px);
  text-shadow: 0 0 6px #ffd70099;
  background: linear-gradient(145deg, #ffffff38, #ffffff14);
  border: 1.5px solid #ffffff40;
  border-radius: 18px;
  margin-bottom: 28px;
  padding: 10px 26px;
  font-size: 28px;
  font-weight: 800;
  display: inline-block;
  box-shadow: 0 4px 16px #00000040, inset 0 0 10px #ffd70040;
}

.btn-game {
  letter-spacing: 1.5px;
  text-transform: uppercase;
  cursor: pointer;
  color: #fff;
  text-shadow: 0 0 6px #ffffffb3;
  border: none;
  border-radius: 14px;
  outline: none;
  padding: 18px 34px;
  font-family: "Press Start 2P", monospace;
  font-size: 16px;
  transition: all .2s;
  position: relative;
  box-shadow: 0 0 12px #ffffff4d;
}

.small {
  letter-spacing: 1.5px;
  padding: 14px 30px;
  font-size: 12px;
}

.btn-game:before {
  content: "";
  -webkit-mask-composite: xor;
  background: linear-gradient(135deg, #8f00ff, #0ff, #fff);
  border-radius: 14px;
  padding: 2px;
  animation: 2s linear infinite glowBorder;
  position: absolute;
  inset: 0;
  -webkit-mask-image: linear-gradient(#fff 0 0), linear-gradient(#fff 0 0);
  -webkit-mask-position: 0 0, 0 0;
  -webkit-mask-size: auto, auto;
  -webkit-mask-repeat: repeat, repeat;
  -webkit-mask-clip: content-box, border-box;
  -webkit-mask-origin: content-box, border-box;
  mask-composite: exclude;
  mask-mode: match-source, match-source;
}

@keyframes glowBorder {
  0% {
    filter: hue-rotate();
  }

  100% {
    filter: hue-rotate(360deg);
  }
}

.btn-game.start {
  background: radial-gradient(circle at 0 0, #00bfff, #7b68ee);
  box-shadow: 0 0 25px #0ff, 0 0 50px #7b68eeb3;
}

.btn-game.start.small {
  background: radial-gradient(circle at 0 0, #00bfff, #7b68ee);
  box-shadow: 0 0 5px #0ff, 0 0 1px #7b68eeb3;
}

.btn-game.start:hover {
  transform: scale(1.05);
  box-shadow: 0 0 35px #0ff, 0 0 90px #7b68ee;
}

.btn-game.start.small:hover {
  background: radial-gradient(circle at 0 0, #00bfff, #7b68ee);
  box-shadow: 0 0 5px #0ff, 0 0 1px #7b68eeb3;
}

.btn-game.stop {
  color: #222;
  text-shadow: 0 0 4px #fff9;
  background: radial-gradient(circle at 100% 100%, #fff700, #fc0);
  box-shadow: 0 0 25px #fff200, 0 0 50px #ff09;
}

.btn-game.stop:hover {
  transform: scale(1.05);
  box-shadow: 0 0 40px #fff700, 0 0 90px #ffff00e6;
}

.btn-edit {
  backdrop-filter: blur(12px);
  color: #fff;
  cursor: pointer;
  background: #ffffff26;
  border: none;
  border-radius: 12px;
  justify-content: center;
  align-items: center;
  width: 38px;
  height: 38px;
  transition: all .25s;
  display: flex;
  position: absolute;
  top: 16px;
  right: 16px;
  box-shadow: 0 4px 20px #00000040;
}

.btn-edit:hover {
  background: #ffffff4d;
  transform: scale(1.1);
  box-shadow: 0 6px 25px #00000059;
}

.btn-edit i {
  color: #fff;
  font-size: 16px;
}

.btn-game:active {
  transform: scale(.93);
  box-shadow: 0 0 10px #fff6;
}

.modal-overlay {
  z-index: 99;
  background: #00000080;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.modal-box {
  color: #616161;
  background: #fff;
  border-radius: 8px;
  width: 50vh;
  max-height: 80vh;
  padding: 20px;
  overflow-y: auto;
}

.nomor-list {
  flex-wrap: wrap;
  gap: 8px;
  margin: 15px 0;
  display: flex;
}

.nomor-item {
  border: 1px solid #ddd;
  border-radius: 4px;
  align-items: center;
  gap: 6px;
  padding: 6px 10px;
  display: flex;
}

.modal-actions {
  justify-content: flex-end;
  gap: 10px;
  margin-top: 15px;
  display: flex;
}

button {
  cursor: pointer;
  padding: 6px 12px;
}

button.actions {
  cursor: pointer;
  color: #fff;
  background: #333;
  border: 1px solid #666;
  border-radius: 6px;
  padding: 6px 12px;
}
